.testimonial {
    padding: 60px 0;
}

.testimonial .titles {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.testimonial .title,
.testimonial .sub-title {
    text-align: center;
}

.testimonial .title {
    font-size: 28px;
    font-weight: 700;
}

.testimonial .sub-title {
    margin-top: 20px;
    font-size: 14px;
    color: #696969;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 600px;
}

.testimonial .sub-title::after {
    position: absolute;
    bottom: -20px;
    content: "";
    width: 80px;
    height: 2px;
    background: linear-gradient(90deg, #07528a, #2379bb);
    border-radius: 10px;
}

.testimonial .sub-title::before{
    position: absolute;
    bottom: -30px;
    content: "";
    width: 40px;
    height: 2px;
    background: linear-gradient(90deg ,#07528a , #2379bb );
    border-radius: 10px;
}

.testimonial .map {
    position: relative;
    margin-top: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.testimonial .map .comments .avatar-container {
    width: 30px;
    height: 30px;
    border-radius: 100%;
    background-position: top;
    background-size: contain;
    background-repeat: no-repeat;
    position: absolute;
    transform: translate(-50%, -50%);
    box-shadow: 0 0 8px rgb(136 136 136 / 80%);
    cursor: pointer;
    z-index: 8;
}

.testimonial .map .comments .comment-text {
    position: absolute;
    top: 0%;
    left: 0%;
    background-color: white;
    border-radius: 10px;
    border-end-start-radius: 0px;
    width: 200px;
    font-size: 12px;
    color: #575151;
    padding: 10px;
    line-height: 16px;
    z-index: 9;
    box-shadow: 0 0 8px rgb(136 136 136 / 15%);
    display: none;
    transition: .5s;
}

.testimonial .map .comments .comment:nth-child(1) .avatar-container {
    background-image: url('../assets/images/client1.jpg');
    top: 47%;
    left: 47%;
}

.testimonial .map .comments .comment:nth-child(1) .avatar-container + .comment-text {
    top: 27%;
    left: 49%;
}

.testimonial .map .comments .comment:nth-child(1) .avatar-container:hover + .comment-text {
    display: block;
}

.testimonial .map .comments .comment:nth-child(2) .avatar-container {
    background-image: url('../assets/images/client2.jpg');
    top: 32%;
    left: 50%;
}

.testimonial .map .comments .comment:nth-child(2) .avatar-container + .comment-text {
    top: 12%;
    left: 52%;
}

.testimonial .map .comments .comment:nth-child(2) .avatar-container:hover + .comment-text {
    display: block;
}

.testimonial .map .comments .comment:nth-child(3) .avatar-container {
    background-image: url('../assets/images/client3.jpg');
    top: 40%;
    left: 70%;
}

.testimonial .map .comments .comment:nth-child(3) .avatar-container + .comment-text {
    top: 20%;
    left: 50%;
    border-radius: 10px;
    border-end-end-radius: 0px;
}

.testimonial .map .comments .comment:nth-child(3) .avatar-container:hover + .comment-text {
    display: block;
}

.testimonial .map .comments .comment:nth-child(4) .avatar-container {
    background-image: url('../assets/images/client4.jpg');
    top: 70%;
    left: 35%;
}

.testimonial .map .comments .comment:nth-child(4) .avatar-container + .comment-text {
    top: 50%;
    left: 37%;
}

.testimonial .map .comments .comment:nth-child(4) .avatar-container:hover + .comment-text {
    display: block;
}

.testimonial .map .comments .comment:nth-child(5) .avatar-container {
    background-image: url('../assets/images/client5.jpg');
    top: 20%;
    left: 25%;
}

.testimonial .map .comments .comment:nth-child(5) .avatar-container + .comment-text {
    top: 0%;
    left: 27%;
}

.testimonial .map .comments .comment:nth-child(5) .avatar-container:hover + .comment-text {
    display: block;
}

@media(max-width:976px){
    .testimonial{
        display: none;
    }
}