@keyframes circle1Anim {
    from {
        transform: scale(0);
        opacity: 0;
    }

    to {
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes circle2Anim {
    from {
        transform: scale(0);
        opacity: 0;
    }

    70% {
        transform: scale(1);
        opacity: 1;
    }

    100%{
        transform: scale(1);
        opacity: .3;
    }
}

@keyframes shape1Anim {
    0% {
        transform: translateY(-50%);
    }

    50% {
        transform: translateY(-70%);
    }

    100% {
        transform: translateY(-50%);
    }
}

@keyframes shape12Anim {
    0% {
        transform: translateY(-50%);
    }

    50% {
        transform: translateY(-30%);
    }

    100% {
        transform: translateY(-50%);
    }
}

@keyframes shape2Anim {
    0% {
        transform: translateX(-50%);
    }

    50% {
        transform: translateX(-70%);
    }

    100% {
        transform: translateX(-50%);
    }
}

@keyframes shape22Anim {
    0% {
        transform: translateX(-50%);
    }

    50% {
        transform: translateX(-30%);
    }

    100% {
        transform: translateX(-50%);
    }
}

@keyframes shape3Anim {
    0% {
        transform: translateX(-10%) translateY(-3%);
    }

    50% {
        transform: translateX(-20%) translateY(20%);
    }

    100% {
        transform: translateX(-10%) translateY(-3%);
    }
}

.home__banner {
    background-image: url('../assets/images/banner-bg.png');
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    min-height: 650px;
    color: white;
    overflow: hidden;
}

.home__banner .container {
    padding-top: 60px;
    height: 100%;
    display: grid;
    grid-template-columns: 450px 1fr;
    align-items: center;
}

.home__banner .container .left-side .title1 {
    font-size: 50px;
    font-weight: 700;
}

.home__banner .container .left-side .text {
    margin-top: 15px;
    font-size: 15px;
    line-height: 22px;
}

.home__banner .container .left-side .btns {
    margin-top: 30px;
}

.home__banner .container .left-side .btns button {
    position: relative;
    padding: 12px 25px;
    font-size: 12px;
    font-weight: 800;
    text-transform: uppercase;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    border: none;
}

.home__banner .container .left-side .btns .primary-btn {
    background-image: linear-gradient(90deg, #20306b, #2a4092);
    border: none;
    z-index: 1;
    border-radius: 5px;
    margin-right: 20px;
}

.home__banner .container .left-side .btns .primary-btn::before {
    position: absolute;
    content: "";
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: linear-gradient(-90deg, #20306b, #2a4092);
    z-index: -1;
    transition: opacity 0.5s linear;
    opacity: 0;
    border-radius: 5px;
}

.home__banner .container .left-side .btns .second-btn {
    background: linear-gradient(90deg, #f2a312, #fd7a23);
    z-index: 1;
    border: none;
    border-radius: 5px;
}

.home__banner .container .left-side .btns .second-btn::before {
    position: absolute;
    content: "";
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: linear-gradient(-90deg, #f2a312, #fd7a23);
    z-index: -1;
    transition: opacity 0.5s linear;
    opacity: 0;
    border-radius: 5px;
}

.home__banner .container .left-side .btns .primary-btn:hover::before {
    opacity: 1;
}

.home__banner .container .left-side .btns .second-btn:hover::before {
    opacity: 1;
}

.home__banner .container .right-side {
    position: relative;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.home__banner .container .right-side .rounded-shape {
    position: absolute;
    width: 80%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    animation-name: circle1Anim;
    animation-duration: 3s;
}

.home__banner .container .right-side .rounded-shape .round1 {
    position: absolute;
    width: 80%;
    height: 0;
    padding-bottom: 80%;
    background-color: #1671b8;
    border-radius: 50%;
    box-shadow: 0 0 30px rgb(50 36 150 / 20%);
}

.home__banner .container .right-side .rounded-shape .round2 {
    position: absolute;
    width: 95%;
    height: 0;
    padding-bottom: 95%;
    background-color: #1671b9;
    border-radius: 50%;
    box-shadow: 0 0 30px rgb(50 36 150 / 5%);
}

.home__banner .container .right-side .rounded-shape .round3 {
    position: absolute;
    width: 110%;
    height: 0;
    padding-bottom: 110%;
    background-color: #1670b970;
    border-radius: 50%;
    box-shadow: 0 0 30px rgb(50 36 150 / 5%);
}

.home__banner .container .right-side .rounded-shape .round4 {
    position: absolute;
    width: 125%;
    height: 0;
    padding-bottom: 125%;
    background-color: #1670b936;
    border-radius: 50%;
    box-shadow: 0 0 30px rgb(50 36 150 / 5%);
    animation-name: circle2Anim;
    /* animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1); */
}

.home__banner .container .right-side .rounded-shape .round5 {
    position: absolute;
    width: 140%;
    height: 0;
    padding-bottom: 140%;
    background-color: #1670b936;
    border-radius: 50%;
    box-shadow: 0 0 30px rgb(50 36 150 / 5%);
    animation-name: circle2Anim;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1);
}

.home__banner .container .right-side .rounded-shape .round6 {
    position: absolute;
    width: 140%;
    height: 0;
    padding-bottom: 140%;
    background-color: #1670b936;
    border-radius: 50%;
    box-shadow: 0 0 30px rgb(50 36 150 / 5%);
    animation-name: circle2Anim;
    animation-duration: 3s;
    animation-delay: 1.5s;
    animation-iteration-count: infinite;
    animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1);
}

.home__banner .container .right-side img {
    width: 80%;
    z-index: 2;
}

.home__banner .container .right-side .shapes {
    position: absolute;
    width: 80%;
    height: 100%;
}

.home__banner .container .right-side .shape1,
.home__banner .container .right-side .shape2,
.home__banner .container .right-side .shape3,
.home__banner .container .right-side .shape4,
.home__banner .container .right-side .shape5 {
    position: absolute;
    width: 18%;
    box-shadow: 0 0 30px rgb(50 36 150 / 30%);
    border-radius: 5px;
}

.home__banner .container .right-side .shape1 {
    left: 0;
    top: 60%;
    transform: translateY(-50%);
    animation-name: shape1Anim;
    animation-duration: 5s;
    animation-iteration-count: infinite;
}

.home__banner .container .right-side .shape2 {
    left: 50%;
    top: -10%;
    transform: translateX(-40%);
    animation-name: shape2Anim;
    animation-duration: 5s;
    animation-iteration-count: infinite;
}

.home__banner .container .right-side .shape3 {
    right: -10%;
    top: -3%;
    transform: translateX(-50%);
    animation-name: shape3Anim;
    animation-duration: 5s;
    animation-iteration-count: infinite;
}

.home__banner .container .right-side .shape4 {
    right: -4%;
    top: 50%;
    transform: translateY(-50%);
    width: 15%;
    animation-name: shape12Anim;
    animation-duration: 5s;
    animation-iteration-count: infinite;
}

.home__banner .container .right-side .shape5 {
    left: 50%;
    bottom: -10%;
    transform: translateX(-50%);
    width: 15%;
    animation-name: shape22Anim;
    animation-duration: 5s;
    animation-iteration-count: infinite;
}

@media(max-width:1200px){
    .home__banner .container .left-side .title1 {
        font-size: 40px;
    }
}

@media(max-width:976px){

    .home__banner .container .left-side .title1 {
        font-size: 30px;
    }

    .home__banner .container {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr;
        justify-items: center;
        padding-top: 120px;
    }

    .home__banner .container .left-side {
        text-align: center;
        max-width: 400px;
    }

    .home__banner .container .right-side {
        justify-content: center;
        max-width: 500px;
    }
}

