footer {
    background: linear-gradient(90deg, #2075b6, #034c83);
    padding: 40px 0;
    color: white;
}

footer .container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

footer .container .logo {
    display: flex;
    justify-content: center;
    align-items: center;
}

footer .container .logo img {
    width: 120px;
}

footer .container .text {
    margin: 30px 0;
    max-width: 640px;
    text-align: center;
    font-size: 13px;
    line-height: 22px;
    letter-spacing: 1px;
}

footer .container .devider {
    width: 100%;
    height: 1px;
    background-color: white;
}

footer .container .bottom-footer {
    margin-top: 30px;
    width: 100%;
    display: grid;
    grid-template-columns: auto auto;
}

footer .container .bottom-footer .copy {
    font-size: 12px;
    letter-spacing: 2px;
}

footer .container .bottom-footer .social-icons {
    display: flex;
    justify-content: flex-end;
}

footer .container .bottom-footer .social-icons svg {
    margin-left: 20px;
    font-size: 16px;
    cursor: pointer;
}

@media(max-width:450px) {

    footer .container .bottom-footer {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr;
        grid-template-areas: 'socialIcons'
            'copy';
        gap: 20px;
    }

    footer .container .bottom-footer .social-icons {
        grid-area: socialIcons;
        justify-content: center;
    }

    footer .container .bottom-footer .copy {
        grid-area: copy;
        text-align: center;
    }
}