.about2{
    padding: 80px 0;
}

.about2 .container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 80px;
}

.about2 .container .right-side{
    display: flex;
    align-items: center;
}

.about2 .container .right-side img{
    width: 100%;
    box-shadow: 0 0 40px rgb(136 136 136 / 20%);
}

.about2 .container .left-side .title{
    font-size: 28px;
    font-weight: 700;
}

.about2 .container .left-side .text{
    margin-top: 30px;
    font-size: 14px;
    color: #696969;
    line-height: 20px;
}

.about2 .container .left-side .cards{
    margin-top: 30px;
}

.about2 .container .left-side .cards .card{
    background: white;
    padding: 10px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    margin: 10px 0;
    cursor: pointer;
    box-shadow: 0 0 8px rgb(136 136 136 / 15%);
}

.about2 .container .left-side .cards .card .icon-container{
    width: 40px;
    height: 40px;
    border-radius: 100%;
    background: linear-gradient(#be6df0, #f67ba4);
    margin-right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.about2 .container .left-side .cards .card:nth-child(2) .icon-container{
    background: linear-gradient(#0da2fc, #00e6f0);
}

.about2 .container .left-side .cards .card:nth-child(3) .icon-container{
    background: linear-gradient(#07c083, #79d405);
}

.about2 .container .left-side .cards .card .icon-container img{
    width: 40%;
}

.about2 .container .left-side .cards .card .text-card{
    font-size: 13px;
    font-weight: 600;
}

@media(max-width:976px) {

    .about2 .container {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr auto;
        grid-template-areas: 'About2Left'
            'About2Right ';

    }

    .about2 .container .left-side {
        grid-area: About2Left;
    }

    .about2 .container .right-side {
        grid-area: About2Right;
    }

    .about2 .container .right-side{
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .about2 .container .right-side img {
        width: 100%;
    }
}