.max-width {
    padding: 0px 120px;
    width: 100%;
    height: 100%;
}

/* scroll up btn style */

.scroll-up-btn {
    position: fixed;
    bottom: 15px;
    right: -50px;
    transition: .5s;
    z-index: 5;
}

.scroll-up-btn.fixed {
    right: 15px;
}

.scroll-up-btn .btn-container {
    position: relative;
    cursor: pointer;
    width: 35px;
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 22px;
    color: white;
    border-radius: 5px;
    background-image: linear-gradient(90deg, #20306b, #2a4092);
    z-index: 1;
    border: 1px solid transparent;
}

.scroll-up-btn .btn-container::before {
    position: absolute;
    content: "";
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: linear-gradient(-90deg, #20306b, #2a4092);
    z-index: -1;
    transition: opacity 0.5s linear;
    opacity: 0;
}

.scroll-up-btn .btn-container:hover:before {
    opacity: 1;
}

section{
    overflow: hidden;
}

@media(max-width:1200px) {
    .max-width {
        padding: 0px 80px;
    }
}

@media(max-width:976px) {
    .max-width {
        padding: 0px 40px;
    }
}

@media(max-width:680px) {
    .max-width {
        padding: 0px 20px;
    }
}