@keyframes circle1Anim {
    from {
        transform: scale(0);
        opacity: 0;
    }

    to {
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes circle2Anim {
    from {
        transform: scale(0);
        opacity: 0;
    }

    70% {
        transform: scale(1);
        opacity: 1;
    }

    100%{
        transform: scale(1);
        opacity: .3;
    }
}

@keyframes shape1Anim {
    0% {
        transform: translateY(-50%);
    }

    50% {
        transform: translateY(-70%);
    }

    100% {
        transform: translateY(-50%);
    }
}

@keyframes shape12Anim {
    0% {
        transform: translateY(-50%);
    }

    50% {
        transform: translateY(-30%);
    }

    100% {
        transform: translateY(-50%);
    }
}

@keyframes shape2Anim {
    0% {
        transform: translateX(-50%);
    }

    50% {
        transform: translateX(-70%);
    }

    100% {
        transform: translateX(-50%);
    }
}

@keyframes shape22Anim {
    0% {
        transform: translateX(-50%);
    }

    50% {
        transform: translateX(-30%);
    }

    100% {
        transform: translateX(-50%);
    }
}

@keyframes shape3Anim {
    0% {
        transform: translateX(-10%) translateY(-3%);
    }

    50% {
        transform: translateX(-20%) translateY(20%);
    }

    100% {
        transform: translateX(-10%) translateY(-3%);
    }
}

.home__banner {
    background-image: url(/ReactJs_LandingApp/static/media/banner-bg.843250b8.png);
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    min-height: 650px;
    color: white;
    overflow: hidden;
}

.home__banner .container {
    padding-top: 60px;
    height: 100%;
    display: grid;
    grid-template-columns: 450px 1fr;
    align-items: center;
}

.home__banner .container .left-side .title1 {
