}

.home__banner .container .left-side .btns button {
    position: relative;
    padding: 12px 25px;
    font-size: 12px;
    font-weight: 800;
    text-transform: uppercase;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    border: none;
}

.home__banner .container .left-side .btns .primary-btn {
    background-image: linear-gradient(90deg, #20306b, #2a4092);
    border: none;
    z-index: 1;
    border-radius: 5px;
    margin-right: 20px;
}

.home__banner .container .left-side .btns .primary-btn::before {
    position: absolute;
    content: "";
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: linear-gradient(-90deg, #20306b, #2a4092);
    z-index: -1;
    transition: opacity 0.5s linear;
    opacity: 0;
    border-radius: 5px;
}

.home__banner .container .left-side .btns .second-btn {
    background: linear-gradient(90deg, #f2a312, #fd7a23);
    z-index: 1;
    border: none;
    border-radius: 5px;
}

.home__banner .container .left-side .btns .second-btn::before {
    position: absolute;
    content: "";
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: linear-gradient(-90deg, #f2a312, #fd7a23);
    z-index: -1;
    transition: opacity 0.5s linear;
    opacity: 0;
    border-radius: 5px;
}

.home__banner .container .left-side .btns .primary-btn:hover::before {
    opacity: 1;
}

.home__banner .container .left-side .btns .second-btn:hover::before {
    opacity: 1;
}

.home__banner .container .right-side {
    position: relative;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.home__banner .container .right-side .rounded-shape {
    position: absolute;
    width: 80%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    animation-name: circle1Anim;
    animation-duration: 3s;
}

.home__banner .container .right-side .rounded-shape .round1 {
    position: absolute;
    width: 80%;
    height: 0;
    padding-bottom: 80%;
    background-color: #1671b8;
    border-radius: 50%;
