}

.home__banner .container .right-side .rounded-shape .round2 {
    position: absolute;
    width: 95%;
    height: 0;
    padding-bottom: 95%;
    background-color: #1671b9;
    border-radius: 50%;
    box-shadow: 0 0 30px rgb(50 36 150 / 5%);
}

.home__banner .container .right-side .rounded-shape .round3 {
    position: absolute;
    width: 110%;
    height: 0;
    padding-bottom: 110%;
    background-color: #1670b970;
    border-radius: 50%;
    box-shadow: 0 0 30px rgb(50 36 150 / 5%);
}

.home__banner .container .right-side .rounded-shape .round4 {
    position: absolute;
    width: 125%;
    height: 0;
    padding-bottom: 125%;
    background-color: #1670b936;
    border-radius: 50%;
    box-shadow: 0 0 30px rgb(50 36 150 / 5%);
    animation-name: circle2Anim;
    /* animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1); */
}

.home__banner .container .right-side .rounded-shape .round5 {
    position: absolute;
    width: 140%;
    height: 0;
    padding-bottom: 140%;
    background-color: #1670b936;
    border-radius: 50%;
    box-shadow: 0 0 30px rgb(50 36 150 / 5%);
    animation-name: circle2Anim;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1);
}

.home__banner .container .right-side .rounded-shape .round6 {
    position: absolute;
    width: 140%;
    height: 0;
    padding-bottom: 140%;
    background-color: #1670b936;
    border-radius: 50%;
    box-shadow: 0 0 30px rgb(50 36 150 / 5%);
