}

.home__banner .container .right-side .shape1,
.home__banner .container .right-side .shape2,
.home__banner .container .right-side .shape3,
.home__banner .container .right-side .shape4,
.home__banner .container .right-side .shape5 {
    position: absolute;
    width: 18%;
