    border-radius: 5px;
}

.home__banner .container .right-side .shape1 {
    left: 0;
    top: 60%;
    transform: translateY(-50%);
    animation-name: shape1Anim;
    animation-duration: 5s;
    animation-iteration-count: infinite;
}

.home__banner .container .right-side .shape2 {
    left: 50%;
    top: -10%;
    transform: translateX(-40%);
    animation-name: shape2Anim;
    animation-duration: 5s;
    animation-iteration-count: infinite;
}

.home__banner .container .right-side .shape3 {
    right: -10%;
    top: -3%;
    transform: translateX(-50%);
    animation-name: shape3Anim;
    animation-duration: 5s;
    animation-iteration-count: infinite;
}

.home__banner .container .right-side .shape4 {
    right: -4%;
    top: 50%;
    transform: translateY(-50%);
    width: 15%;
    animation-name: shape12Anim;
    animation-duration: 5s;
    animation-iteration-count: infinite;
}

.home__banner .container .right-side .shape5 {
    left: 50%;
    bottom: -10%;
    transform: translateX(-50%);
    width: 15%;
    animation-name: shape22Anim;
    animation-duration: 5s;
    animation-iteration-count: infinite;
}

