nav{
    position: fixed;
    z-index: 99;
    color: white;
    width: 100%;
    padding: 20px 0;
    transition: .5s;
}

nav.fixed{
    background: linear-gradient(90deg, #07528a, #2379bb);
    box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.15);
    padding: 10px 0;
    transition: .5s;
}

nav .container{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

nav .container .logo{
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

nav .container .logo img{
    width: 80px;
    transition: .5s;
}

nav.fixed .container .logo img{
    width: 80px;
}

nav .container .menu{
    display: flex;
}

nav .container .menu li{
    margin-left: 40px;
    font-size: 15px;
    font-weight: 500;
    cursor: pointer;
    transition: .5s;
}

nav.fixed .container .menu li{
    font-size: 15px;
}

nav .container .menu-icon{
    display: none;
    justify-content: center;
    align-items: center;
}

nav .container .menu-icon svg{
    font-size: 18px;
    cursor: pointer;
}

@media(max-width:976px){

    nav .container .menu-icon{
        display: flex;
        z-index: 9999;
    }

    nav .container .menu-icon svg:nth-child(2){
        display: none;
    }

    nav .container .menu-icon.active svg:nth-child(1){
        display: none;
    }

    nav .container .menu-icon.active svg:nth-child(2){
        display: block;
    }

    nav .container .menu{
        position: fixed;
        height: 100vh;
        width: 30%;
        min-width: 200px;
        top: 0;
        right: -100%;
        background-color: #07528a;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        padding: 100px 0px;
        transition: 0.5s all;
        z-index: 999;
        border-start-start-radius: 10px;
        border-end-start-radius: 10px;
    }

    nav .container .menu.active{
        right: 0%;
    }

    nav .container .menu li{
        margin-bottom: 20px;
        margin-left: 20px;
    }

}

