.services {
    padding: 100px 0;
}

.services .container {
    margin-top: 100px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 40px;
    gap: 40px;
}

.services .title,
.services .sub-title{
    text-align: center;
}

.services .title{
    font-size: 28px;
    font-weight: 700;
}

.services .sub-title{
    margin-top: 20px;
    font-size: 14px;
    color: #696969;
    position: relative;
    display: flex;
    justify-content: center;
}

.services .sub-title::after{
    position: absolute;
    bottom: -20px;
    content: "";
    width: 80px;
    height: 2px;
    background: linear-gradient(90deg ,#07528a , #2379bb );
    border-radius: 10px;
}

.services .sub-title::before{
    position: absolute;
    bottom: -30px;
    content: "";
    width: 40px;
    height: 2px;
    background: linear-gradient(90deg ,#07528a , #2379bb );
    border-radius: 10px;
}

.services .container .card {
    position: relative;
    border-radius: 5px;
    box-shadow: 0 0 10px rgb(136 136 136 / 15%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 60px 30px;
    overflow: hidden;
    cursor: pointer;
}

.services .container .card:nth-child(2) {
    box-shadow: 0 0 30px rgb(136 136 136 / 20%);
}

.services .container .card::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: -100%;
    width: 100%;
    height: 3px;
    transition: .5s;
    background: linear-gradient(90deg, #07528a, #2379bb);
}

.services .container .card.active::before {
    left: 0%;
}

.services .container .card:hover::before {
    left: 0%;
}

.services .container .card .icon-container {
    width: 70px;
    height: 70px;
    background: linear-gradient(90deg, #07528a, #2379bb);
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.services .container .card .icon-container::before {
    content: '';
    position: absolute;
    width: 90px;
    height: 90px;
    border-radius: 100%;
    background: linear-gradient(90deg, #07528a, #2379bb);
    opacity: 0.2;
    transition: .5s;
}

.services .container .card:hover .icon-container::before {
    opacity: 0;
}

.services .container .card .icon-container::after {
    content: '';
    position: absolute;
    width: 110px;
    height: 110px;
    border-radius: 100%;
    background: linear-gradient(90deg, #07528a, #2379bb);
    opacity: 0.08;
}

.services .container .card:hover .icon-container::after {
    animation-name: overlayIcon;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-timing-function: cubic-bezier(0.77, 0, 0.175, 1);
}

.services .container .card .icon-container img {
    width: 50%;
}

.services .container .card .title {
    margin-top: 30px;
    font-size: 20px;
    font-weight: 700;
}

.services .container .card .text {
    margin-top: 20px;
    font-size: 14px;
    line-height: 20px;
    font-weight: 400;
    text-align: center;
    color: #696969;
}

