.testimonial .map {
    position: relative;
    margin-top: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.testimonial .map .comments .avatar-container {
    width: 30px;
    height: 30px;
    border-radius: 100%;
    background-position: top;
    background-size: contain;
    background-repeat: no-repeat;
    position: absolute;
    transform: translate(-50%, -50%);
    box-shadow: 0 0 8px rgb(136 136 136 / 80%);
    cursor: pointer;
    z-index: 8;
}

.testimonial .map .comments .comment-text {
    position: absolute;
