    left: 0%;
    background-color: white;
    border-radius: 10px;
    border-end-start-radius: 0px;
    width: 200px;
    font-size: 12px;
    color: #575151;
    padding: 10px;
    line-height: 16px;
    z-index: 9;
    box-shadow: 0 0 8px rgb(136 136 136 / 15%);
    display: none;
    transition: .5s;
}

.testimonial .map .comments .comment:nth-child(1) .avatar-container {
    background-image: url(data:image/jpeg;base64,);
    top: 47%;
    left: 47%;
}

.testimonial .map .comments .comment:nth-child(1) .avatar-container + .comment-text {
    top: 27%;
    left: 49%;
}

.testimonial .map .comments .comment:nth-child(1) .avatar-container:hover + .comment-text {
    display: block;
}

