.contactUs {
    padding: 100px 0;
}

.contactUs .container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 50px;
    gap: 50px;
}

.contactUs .container .right-side img{
    width: 90%;
}

.contactUs .container .left-side .title{
    font-size: 28px;
    font-weight: 700;
}

.contactUs .container .left-side .sub-title{
    margin-top: 20px;
    font-size: 14px;
    color: #696969;
    position: relative;
    display: flex;
    justify-content: flex-start;
}

.contactUs .container .left-side .sub-title::after{
    position: absolute;
    bottom: -20px;
    content: "";
    width: 80px;
    height: 2px;
    background: linear-gradient(90deg ,#07528a , #2379bb );
    border-radius: 10px;
}

.contactUs .container .left-side .sub-title::before{
    position: absolute;
    bottom: -30px;
    content: "";
    width: 40px;
    height: 2px;
    background: linear-gradient(90deg ,#07528a , #2379bb );
    border-radius: 10px;
}

.contactUs .container .left-side .inputs{
    margin-top: 60px;
}

.contactUs .container .left-side .inputs input,
.contactUs .container .left-side .inputs textarea{
    padding: 8px 10px;
    border: 1px solid #c9c9c9;
    border-radius: 5px;
    width: 100%;
    transition: .5s;
}

.contactUs .container .left-side .inputs input:focus,
.contactUs .container .left-side .inputs textarea:focus{
    border: 1px solid #2379bb;
}

.contactUs .container .left-side .inputs textarea{
    margin-top: 15px;
}

.contactUs .container .left-side .inputs .row1{
    display: flex;
    grid-gap: 15px;
    gap: 15px;
    margin-bottom: 15px;
}

.contactUs .container .left-side .inputs .row1 input{
    width: 50%;
}

.contactUs .container .left-side .btns button {
    position: relative;
    padding: 12px 25px;
    margin-right: 20px;
    font-size: 12px;
    font-weight: 800;
    text-transform: uppercase;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    border: none;
    margin-top: 15px;
}

.contactUs .container .left-side .btns .primary-btn {
    background-image: linear-gradient(90deg, #20306b, #2a4092);
    border: none;
    z-index: 1;
}

.contactUs .container .left-side .btns .primary-btn::before {
    position: absolute;
    content: "";
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: linear-gradient(-90deg, #20306b, #2a4092);
    z-index: -1;
    transition: opacity 0.5s linear;
    opacity: 0;
    border-radius: 5px;
}

.contactUs .container .left-side .btns .primary-btn:hover::before {
    opacity: 1;
}

@media(max-width:976px){

    .contactUs {
        padding: 60px 0;
    }

    .contactUs .container{
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
    }

    .contactUs .container .right-side {
        display: none;
    }

    .contactUs .container .left-side {
        text-align: center;
    }

    .contactUs .container .left-side .sub-title{
        justify-content: center;
    }

    .contactUs .container .left-side .btns{
        text-align: start;
    }
}
footer {
    background: linear-gradient(90deg, #2075b6, #034c83);
    padding: 40px 0;
    color: white;
}

footer .container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

footer .container .logo {
    display: flex;
    justify-content: center;
    align-items: center;
}

footer .container .logo img {
    width: 120px;
}

footer .container .text {
    margin: 30px 0;
    max-width: 640px;
    text-align: center;
    font-size: 13px;
    line-height: 22px;
    letter-spacing: 1px;
}

footer .container .devider {
    width: 100%;
    height: 1px;
    background-color: white;
}

footer .container .bottom-footer {
    margin-top: 30px;
    width: 100%;
    display: grid;
    grid-template-columns: auto auto;
}

footer .container .bottom-footer .copy {
    font-size: 12px;
    letter-spacing: 2px;
}

footer .container .bottom-footer .social-icons {
    display: flex;
    justify-content: flex-end;
}

footer .container .bottom-footer .social-icons svg {
    margin-left: 20px;
    font-size: 16px;
    cursor: pointer;
}

@media(max-width:450px) {

    footer .container .bottom-footer {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr;
        grid-template-areas: 'socialIcons'
            'copy';
        grid-gap: 20px;
        gap: 20px;
    }

    footer .container .bottom-footer .social-icons {
        grid-area: socialIcons;
        justify-content: center;
    }

    footer .container .bottom-footer .copy {
        grid-area: copy;
        text-align: center;
    }
}
